import { URLSearchParams } from "url";
import { type NextRequest, NextResponse } from "next/server";

export async function GET(request: NextRequest) {
	const url = "https://export.arxiv.org/api/query";
	const query = request.nextUrl.searchParams.get("query");
	const start = request.nextUrl.searchParams.get("start");
	const max_results = request.nextUrl.searchParams.get("max_results");

	const searchParams = new URLSearchParams();
	searchParams.set("search_query", `all:${query}`);
	searchParams.set("start", start || "0");
	searchParams.set("max_results", max_results || "10");
	const fullUrl = `${url}?${searchParams.toString()}`;

	const response = await fetch(fullUrl);
	const data = await response.text();
	console.log(data);

	// ?search_query=all:neural%20networks%20and%20mlp&start=0&max_results=";

	return NextResponse.json({
		message: "Hello, world!",
	});
}
